.adminContainer {
	max-width: 1200px;
	margin: 0 auto;
	padding: 20px;
}

.title {
	text-align: center;
	font-size: 32px;
	margin-bottom: 20px;
}

.searchInput {
	width: 100%;
	padding: 10px;
	font-size: 16px;
	margin-bottom: 20px;
	border: 1px solid #ccc;
	border-radius: 4px;
}

.trainTable {
	width: 100%;
	border-collapse: collapse;
	margin-bottom: 20px;
}

.trainTable th,
.trainTable td {
	padding: 10px;
	border: 1px solid #ddd;
	text-align: left;
}

.trainTable th {
	background-color: #f4f4f4;
}

.editButton,
.deleteButton {
	padding: 5px 10px;
	font-size: 14px;
	cursor: pointer;
	margin-right: 5px;
	border: none;
	border-radius: 4px;
}

.editButton {
	background-color: #4caf50;
	color: white;
}

.deleteButton {
	background-color: #f44336;
	color: white;
}

.formTitle {
	font-size: 24px;
	margin-bottom: 20px;
}

.form {
	display: flex;
	flex-direction: column;
	gap: 10px;
}

.formInput {
	padding: 10px;
	font-size: 16px;
	border: 1px solid #ccc;
	border-radius: 4px;
}

.submitButton {
	padding: 10px;
	font-size: 18px;
	color: white;
	background-color: #007bff;
	border: none;
	border-radius: 4px;
	cursor: pointer;
}

.submitButton:hover {
	background-color: #0056b3;
}
